@font-face {
  font-family: 'MusticaProSemiBold';
  src: local('MusticaProSemiBold'), url(./fonts/MusticaProSemiBold.otf) format('opentype');
}
@font-face {
  font-family: 'Roadway';
  src: local('Roadway'), url(./fonts/Roadway.ttf) format('truetype');
}
@font-face {
  font-family: 'DINMediumRegular';
  src: local('DINMediumRegular'), url(./fonts/DINMediumRegular.ttf) format('truetype');
}

.App > * {
  background-color: rgba(0, 0, 0, 0.3);
}

/* -------------------------- INDEX BAR -------------------------- */

.index-bar {
  width: 100%;
  background-color: rgba(58, 165, 63, 0.231);
  position: fixed;
  top: 0;

  /* Display index bar buttons in row */
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 15px;

  height: 100px;

  backdrop-filter: blur(10px);
}

.index-bar-button {
  text-decoration: none;
  font-family: 'DINMediumRegular';
  text-transform: uppercase;
  color:rgb(200, 212, 255);
  padding: 0px 5px;
  cursor: pointer;
  max-width: 200px;
  
  /* Display text in center */
  display: flex;
  align-items: center;
  text-align: center;
  }

.index-bar-button:hover {
  color: white;
}

/* -------------------------- GENERIC STYLES -------------------------- */

h2 {
  font-size: 50pt;
  margin: 0pt;
  font-family: 'MusticaProSemiBold';
  color: white;
}

.subtle-text {
  font-size: 20pt;
  font-family: 'DINMediumRegular';
  color: rgb(190, 190, 190);
}

body {
  background-color: rgb(39, 39, 39);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* -------------------------- SECTION CONTAINER -------------------------- */

.section-container {
  position: absolute;
  top: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 50px;
}

.section-container > * {
  background-color: rgba(255, 127, 80, 0.2);
}

/* -------------------------- HOME SECTION -------------------------- */

#home-section {
  /* Handle index bar and intro text area */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.home-text {
  min-width: 600px;
  width: 60%;

  /* Display text in center */
  display: flex;
  justify-content: center;
  flex-direction: column;

  background-color: rgba(0, 0, 255, 0.1);
}

.home-first-name {
  font-size: 108pt;
  font-family: 'MusticaProSemiBold';
  text-transform: uppercase;
  color: white;
  margin: 0;
  padding-left: 16px;
  letter-spacing: 15px;

  /* background-color: rgba(0, 255, 255, 0.327); */
}

.home-last-name {
  font-size: 108pt;
  font-family: 'MusticaProSemiBold';
  text-transform: uppercase;
  color: white;
  margin: 0;
  letter-spacing: 4px;

  /* background-color: rgba(0, 255, 255, 0.327); */
}

.home-subtitle {
  font-size: 16pt;
  font-family: 'Roadway';
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;

  /* background-color: rgba(0, 255, 255, 0.327); */
}

/* -------------------------- ABOUT SECTION -------------------------- */

#about-section {
  display: flex;
  flex-direction: column;
}

/* -------------------------- EXPERIENCE SECTION -------------------------- */

#experience-section {
  display: flex;
  flex-direction: column;
}

/* -------------------------- PROJECTS SECTION -------------------------- */

#projects-section {
  margin: 40px;
  padding: 50px 0px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 25px;
}

#projects-section > .subtle-text
{
  max-width: 60%;
}

.project-list{
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;

  background-color: rgba(43, 226, 98, 0.5);
}

.project-block {
  width: 370px;
  height: 370px;
  padding: 15px;

  display: flex;
  flex-direction: column;

  border-radius: 8px;

  background-color: rgba(137, 43, 226, 0.5);
}

.project-block-picture {
  overflow: hidden;
  border-radius: 8px 8px 0px 0px;


  width: 100%;
  height: 60%;
  background-color: rgba(38, 233, 226, 0.5);
}

.project-block-picture > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-block-caption-area {
  overflow: hidden;
  border-radius: 0px 0px 8px 8px;
  width: 100%;
  height: 40%;
  background-color: rgba(205, 226, 43, 0.5);
}

/* -------------------------- CONTACT SECTION -------------------------- */

#contact-section {
  margin: 40px;
  padding: 50px 0px;
  /*box-shadow: inset 0em 0em 4em rgba(0, 0, 0, 0.5);*/

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 30px;
}

#contact-section > * {
  background-color: rgba(0, 128, 0, 0.1);
}

#contact-section > .subtle-text {
  max-width: 60%;
}

.email-bar {
  height: 50px;

  border-style: solid;
  border-width: 2px;
  border-radius: 4px;
  border-color: rgb(190, 190, 190);
  color: rgb(190, 190, 190);

  font-size: 20pt;
  font-family: 'DINMediumRegular';

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.email-bar p {
  padding: 0px 20px;
  -webkit-user-select: all;
  -ms-user-select: all;
  user-select: all;
}

.email-bar div {
  background-color: rgb(49, 49, 49);
}

.email-bar div img {
  cursor: pointer;
  max-width:40px;
  max-height:40px;
  padding: 5px;
  vertical-align: middle;
  filter:brightness(74.5%);
}

.email-bar div:hover {
  background-color: rgb(96, 96, 96);
}

.email-bar div:hover img {
  filter:brightness(100%);
}

.social-media-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social-media-section > p {
  padding-bottom: 20px;
}

.contact-site-button {
  height: 50px;
  width: 200px;
  padding: 10px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 20px;
}

.contact-site-button > img {
  max-width:100%;
  max-height:100%;
  filter:brightness(74.5%);
}

.contact-site-button > p {
  font-size: 20pt;
  text-transform: uppercase;
  font-family: 'DINMediumRegular';
  color: rgb(190, 190, 190);
}

.contact-site-button:hover > img {
  filter:brightness(100%);
}

.contact-site-button:hover > p {
  color: white;
}
